import { WrapperContainer } from "../../../App-styled";
import styled from "./style.module.css";
import "react-slideshow-image/dist/styles.css";
import "./style.css";
import { useTranslation } from "react-i18next";
import { Slide } from "react-slideshow-image";
import img1 from "../../../assets/hero/1.png";
import img2 from "../../../assets/hero/2.png";
import img3 from "../../../assets/hero/3.png";
function HomeHero() {
  const settings = {
    dots: false,
    autoplay: true,
    infinite: true,
    speed: 1000,
    slidesToShow: 1,
    slidesToScroll: 1,
  };
  const { t, i18n } = useTranslation();
  return (
    <>
      <div className="Slide">
        <Slide
          ref={{
            current: "[Circular]",
          }}
          indicators
          nextArrow={
            <button
              className="pri"
              style={{
                background: "none",
                border: "0px",
                width: "30px",
              }}
            >
              <svg
                fill="#fff"
                viewBox="0 0 512 512"
                xmlns="http://www.w3.org/2000/svg"
                className="svv"
              >
                <path d="M512 256L270 42.6v138.2H0v150.6h270v138z" />
              </svg>
            </button>
          }
          prevArrow={
            <button
              className="next"
              style={{
                background: "none",
                border: "0px",
                width: "30px",
              }}
            >
              <svg
                fill="#fff"
                viewBox="0 0 512 512"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="M242 180.6v-138L0 256l242 213.4V331.2h270V180.6z" />
              </svg>
            </button>
          }
        >
          <div className="each-slide-effect">
            <div
              className={styled.backdi}
              style={{
                backgroundImage: `url(${img3})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                width: "100%",
                height: "100%",
              }}
            >
              <WrapperContainer>
                <div className={styled.Wrapper}>
                  <h2>{t("HomeHero.0")}</h2>
                  <p> {t("HomeHero.1")}</p>
                  <button>
                    {" "}
                    <a href="tel:+998-93-500-55-51">{t("HomeHero.2")}</a>
                  </button>
                </div>
              </WrapperContainer>
            </div>
          </div>
          <div className="each-slide-effect">
            <div
              className={styled.backdv}
              style={{
                backgroundImage: `url(${img2})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                width: "100%",
                height: "100% ",
              }}
            >
              <WrapperContainer>
                <div className={styled.Wrapper}>
                  <h2>
                  {t("HomeHero.3")}
                  </h2>
                  <p>{t("HomeHero.4")}</p>
                  <button>
                    {" "}
                    <a href="tel:+998-93-500-55-51">{t("HomeHero.2")}</a>
                  </button>
                </div>
              </WrapperContainer>
            </div>
          </div>
          <div className="each-slide-effect">
            <div
              style={{
                backgroundImage: `url(${img1})`,
                backgroundRepeat: "no-repeat",
                backgroundSize: "cover",
                width: "100% !important",
                height: "100%",
              }}
              className={styled.backdiv}
            >
              <WrapperContainer>
                <div className={styled.Wrapper}>
                  <h2>
                  {t("HomeHero.5")}
                  </h2>
                  <p>{t("HomeHero.6")}</p>
                  <button>
                    {" "}
                    <a href="tel:+998-93-500-55-51">{t("HomeHero.2")}</a>
                  </button>
                </div>
              </WrapperContainer>
            </div>
          </div>
        </Slide>
      </div>
    </>
  );
}

export default HomeHero;
