import React, { useState } from "react";
import { WrapperContainer } from "../../App-styled";
import BigImg from "../../assets/more/big.png";
import MiniImg from "../../assets/more/min.png";
import styled from "./style.module.css";
import "./style.css";
import { Table } from "antd";
import img from "../../assets/products/product.png";
import bigLine from "../../assets/products/line.png";
import { Col, Row } from "react-grid-system";
import { useDispatch, useSelector } from "react-redux";
import Cookies from "universal-cookie";
import { useEffect } from "react";
import { GetProducts } from "../../redux/store/products";
import { useTranslation } from "react-i18next";

function MoreComponent() {
  const cookies = new Cookies();
  const [btn, setBtn] = useState(false);
  const data = useSelector((state) => state.products.getProducts.Data.data);
  const ProductsId = () => {
    return cookies.get("ProductId");
  };
  const Lang = cookies.get("i18next");
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(GetProducts());
  }, []);
  const { t } = useTranslation();

  return (
    <>
      <div className="morrr">
        <div className={styled.WrapperBack}>
          <WrapperContainer>
            {data?.map((elem, index) =>
              elem.id == ProductsId() ? (
                <>
                  <div className={styled.Wrapper} key={index}>
                    <div className={styled.moreImgs}>
                      {elem.images.map((ele) => (
                        <img
                          src={ele.image}
                          className={styled.megaImg}
                          alt="photo"
                        />
                      ))}
                      <div>
                        {elem.images.map((ele) => (
                          <img src={ele.image} alt="photo" />
                        ))}
                      </div>
                    </div>
                    <div className={styled.infors}>
                      <div className={styled.header}>
                        <div className={styled.title}>
                          <h2>{elem.title}</h2>
                        </div>
                        <div className={styled.start}>
                          <p>В наличии</p>
                          <h3>Цена: Договорная</h3>
                          <button>
                            <a href="tel:+998-93-500-55-51">Связаться с нами</a>
                          </button>
                        </div>
                      </div>
                      <div className={styled.decription}>
                        <p>
                          {Lang == "ru" ? (
                            <>{elem.description_ru}</>
                          ) : Lang == "en" ? (
                            <>{elem.description_en}</>
                          ) : Lang == "uz" ? (
                            <>{elem.description_uz}</>
                          ) : null}
                        </p>
                      </div>
                    </div>
                  </div>
                </>
              ) : null
            )}
            <div className={styled.Cards}>
              <h2>
                Другие продукции <img src={bigLine} alt="photo" />
              </h2>
              <Row style={{ margin: 0, padding: 0 }}>
                {data?.map((elem) => (
                  <Col lg={4} md={6} sm={12}>
                    <div className={styled.card}>
                      {elem.images.map((ele) => (
                        <img
                          src={ele.image}
                          className={styled.megaImg}
                          alt="photo"
                        />
                      ))}
                      <div className={styled.texts}>
                        <h3>{elem.title}</h3>
                        <button
                          onClick={() => {
                            cookies.set("ProductId", elem.id);
                            window.location.reload();
                          }}
                        >
                          <span>{t("Our.4")}</span>
                          <p>
                            <i class="bx bx-right-arrow-alt"></i>
                          </p>
                        </button>
                      </div>
                    </div>
                  </Col>
                ))}
              </Row>
            </div>
          </WrapperContainer>
        </div>
      </div>
    </>
  );
}

export default MoreComponent;
